import { create } from "zustand";

export const useBeerStore = create((set) => ({
  beers: [],
  setBeer: (newBeer) => {
    set((state) => {
      // ,         
      if (JSON.stringify(state.beers) === JSON.stringify(newBeer)) {
        return state; //   ,     
      } else {
        return { beers: [...state.beers, ...newBeer] }; //   ,     
      }
    });
  },
  removeItems: (ids) => {
    set((state) => ({
      beers: state.beers.filter((item) => !ids.includes(item.id)),
    }));
  },
}));
